.btns{
    width: 100%;
    display: flex;
    justify-content: space-around;
    padding: 10px 5px 5px;
    align-items: center;
    max-width: 400px;
    margin: 15px auto 0;
}

.btn{
    text-transform: uppercase;
    font-size: 22px;
    font-weight: 700;
    position: relative;
    cursor: pointer;
    &::after{
        content: "";
        width: 0;
        height: 2px;
        position: absolute;
        bottom: 0;
        left: 0;
        transition: width ease 0.2s;
    }
}

.plus{
    color: #31a626;
    &.active{
        &::after{
            background-color: #31a626;
            width: 100%;
        }
    }
}

.minus{
    color: #d40202;
    &.active{
        &::after{
            background-color: #d40202;
            width: 100%;
        }
    }
}